// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// подключения компонентов страницы
@import "./components/header";



.vertical-row{
    height: 100%;
    width:50px;
    overflow: hidden;
    padding-right: 30px;
    
    background: #B3FF00;
    
  }
  .horizontal-row{
      width: 110%;
      height: 50px;
      left: -50px;
      overflow: hidden;
      background: #B3FF00;
      position: absolute;
      transform: rotate(344deg);
      bottom: 150px;
      span{
        font-family: 'IBM Plex Mono', monospace;
        font-weight: bold;
        text-align:center;
        font-size: 18px;
        display: inline-block;
        text-transform: uppercase;
        padding-top: 15px;
        animation: text 2500s infinite linear;
        transform: rotate(122deg);
        white-space: nowrap;
      }
  }
  .about-us__vertycal-line{
    height: 600px;
    position: absolute;
    right: 0;
    width:50px;
    overflow: hidden;
    padding-right: 30px;
    
    background: #B3FF00;
    span{
        font-family: 'IBM Plex Mono', monospace;
        font-weight: bold;
        text-align:center;
        font-size: 18px;
        display: inline-block;
        text-transform: uppercase;
        padding-top: 30px;
        animation: text 2500s infinite linear;
        transform: rotate(122deg);
        white-space: nowrap;
      }
  }

  .about-us__vertycal-line2{
    height: 600px;
    position: absolute;
    left: 0;
    top: 1600px;
    width:50px;
    overflow: hidden;
    padding-right: 30px;
    
    background: #B3FF00;
    span{
        font-family: 'IBM Plex Mono', monospace;
        font-weight: bold;
        text-align:center;
        font-size: 18px;
        display: inline-block;
        text-transform: uppercase;
        padding-top: 30px;
        animation: text 2500s infinite linear;
        transform: rotate(122deg);
        white-space: nowrap;
      }
  }
  .for-rotate{
     
    transform: rotate(-90deg);
  }
  .vertical-row span{
    font-family: 'IBM Plex Mono', monospace;
    font-weight: bold;
    text-align:center;
    font-size: 18px;
    display: inline-block;
    text-transform: uppercase;
    padding-top: 35px;
    animation: text 2500s infinite linear;
    transform: rotate(122deg);
    white-space: nowrap;
  }
  @keyframes text {
    0%{
      transform: translate(0, 0);
    }
    
    100%{
      transform: translate(-160%, 0);
    }
  }
  .main-screen{
    background: linear-gradient(180deg, #111111 0%, #673AB7 100%);
    height: 90vh;
    position: relative;
  }
  .main-screen__block-subtitle{
    font-family: 'IBM Plex Mono';
    font-style: normal;
    font-weight: normal;
    font-size: 12.8px;
    line-height: 19px;
    /* identical to box height, or 150% */
    
    letter-spacing: 6px;
    
    color: rgba(255, 255, 255, 0.25);
  }
  .main-screen__block{
      position: absolute;
      display: flex;
      flex-direction: column;
      align-items: center;

      top: 30%;
      left: 50%;
      transform: translate(-50%, -30%);
  }
  .main-screen__block-title{
    font-family: 'Monument Extended Ultrabold';
      margin:14px 0 24px 0;
     font-weight: 800;
     font-size: 96px;
     line-height: 115px;
     background: linear-gradient(90deg, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0.8) 49.27%, rgba(255, 255, 255, 0.4) 100%);
     -webkit-background-clip: text;
     -webkit-text-fill-color: transparent;
  }
  .main-screen__block-descr{
    font-family: 'IBM Plex Mono';
   
    font-size: 16px;
    line-height: 24px;
    color: rgba(255, 255, 255, 0.5);
    letter-spacing: 2px;
  }
  .btn{
      font-family: 'GraphikLCG-Medium';
      background: #673AB7;
      border:4px solid transparent;
      display: inline-block;
      border-radius: 14px;
      width: 311px;
      padding:15px 0 15px 0;
      
      font-size: 17px;
      color: #FFFFFF;
      margin-top: 63px;
      cursor: pointer;
      transition: all .5s linear;
      &:hover{
        border: 4px solid #4E2D87;
      }
      a{
          color: inherit;
      }
  }
  .main-sreen__block-button-descr{
    font-size: 12px;
    line-height: 20px;

    font-family: 'GraphikLCG-Regular';
    letter-spacing: -0.24px;
    color: #8E8E93;
    margin-top: 7px;
    a{  
        font-family: 'GraphikLCG-Semibold';
        color: #007AFF;
        
        &:hover{
            text-decoration: underline;
        }
    }
  }
  .about-us{
      background: #111111;
      padding-top: 100px;
      padding-bottom: 141px;
     
      
  }
  .about-us__block-item{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 64px;
  }
  .about-us__item-text-icon{
      width: 64px;
      height: 64px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #1E1E1E;
      border-radius: 13px;
  }
  .about-us__item-text{
      width: 561px;
  }
  .about-us__item-title{
    font-family: 'GraphikLCG-Semibold';
    
    font-size: 36px;
    line-height: 20px;
    margin:50px 0;
    letter-spacing: -0.24px;
    color: #FFFFFF;
    
  }
  .about-us__item-img{
      img{
        border-radius: 13px;
      }
  }
  .about-us__item-descr{
      font-family: 'GraphikLCG-Regular';
      max-width: 423px;
      font-size: 18px;
      line-height: 20px;
      letter-spacing: -0.24px;
      color: #FFFFFF;
  }
  .faq{
      background: #111111;
      padding-bottom: 22px;

  }
  .faq__title{
      text-align: center;
      
      font-size: 36px;
      line-height: 48px;
      letter-spacing: -0.24px;
      color: #FFFFFF;
      font-family: 'GraphikLCG-Semibold';
  }
  .faq__block{
      margin-top: 88px;
      display: flex;
      justify-content: space-between;
  }
  .faq__block-item{
    border: 3px solid #303030;
    border-radius: 13px;
    padding: 15px;
    height: 214px;
  }
  .faq__item-title{
    font-family: 'GraphikLCG-Semibold';
    font-weight: 600;
    font-size: 18px;
    line-height: 48px;
    letter-spacing: -0.24px;
    color: #FFFFFF;
  }
  .faq__item-descr{
    font-size: 14px;
    line-height: 15px;
    letter-spacing: -0.24px;
    font-family: 'GraphikLCG-Regular';
    color: #FFFFFF;
    max-width: 330px;
  }
  .faq__links{
    font-family: 'GraphikLCG-Regular';
      text-align: center;
      font-size: 12px;
      line-height: 20px;
      letter-spacing: -0.24px;
      color: #8E8E93;
      margin-top: 30px;
      
      a{
        font-family: 'GraphikLCG-Semibold';
        color: #007AFF;
        
        &:hover{
            text-decoration: underline;
        }
      }
  }


  .footer{ 
background: #202020;
  }
  .footer__wrapper{
      
      align-items: center;
      display: flex;
      justify-content: space-between;
  }
  .footer__info-ul{
      display: flex;
      flex-direction: column;
      align-items: center;
      li{
        font-family: 'GraphikLCG-Regular';
        font-size: 14px;
        line-height: 20px;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: -0.24px;
        
        color: rgba(255, 255, 255, 0.5);
        a{
            margin-left: 5px;
            text-decoration: underline;
            color: inherit;
            &:hover{
                color: rgba(255, 255, 255, 1);
            }
        }
        
      }
  }
  .footer__info{
      margin-right: -120px;
  }
  .footer__links-ul{
      display: flex;
      li{
        font-family: 'GraphikLCG-Regular';
        margin-left: 16px;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: -0.24px;
        color: #007BFF;
        a{
            color: inherit;
            &:hover{
                text-decoration: underline;
            }
        }
      }
  }
  .footer__logo{
    font-family: 'GraphikLCG-Semibold';
   
    font-size: 14px;
    line-height: 20px;
    letter-spacing: -0.24px;
    color: #FFFFFF;
  }

@media (max-width: 1420px){
  .about-us__vertycal-line2{
    
  }
  .about-us__vertycal-line{
   
  }
  .about-us__block-item{
    flex-direction: column;
  }
  .about-us__item-text{
    order:1;
  }
  .about-us__item-descr{
    max-width: 100%;
  }
  .about-us__item-img img{
    margin-bottom: 25px;
  }
  .about-us__item-text{
    width: 640px;
  }
}
@media (max-width: 1024px){
  .main-screen {
    height: 100vh;
  }
  .vertical-row{
    display: none;
  }
  .main-screen__block-title{
    font-size: 80px;
  }
  .faq__block{
    flex-direction: column;
    align-items: center;
  }
  .faq__block-item{
    margin-bottom: 25px;
    width: 100%;
    
  }
  
  .horizontal-row{
    transform: rotate(0deg);
    bottom: -22px;
  }
  .about-us__vertycal-line{
    display: none;
  }
  .about-us__vertycal-line2{
    display: none;
  }
  .main-screen__block{
    top: 40%;
  }
  .scroll-btn{
    margin-top: 13px;
  }
  .main-screen__block-title{
    margin-bottom: 0;
  }
  
}
@media(max-width: 772px){
  .main-screen__block-title{
    font-size: 66px;
  }
  .main-screen__block-descr {
    font-size: 14px;
  }
 
}
@media (max-width: 656px){
  .about-us__item-text{
    width: 100%;
  }
  .main-screen{
    height: 100vh;
  }
  .scroll-btn{
    margin-top: 60px;
  }
}
@media (max-width: 640px){
  .main-screen__block-title{
    margin:0;
    font-size: 46px;
    line-height: 1.5;
  }
  .main-screen__block-descr{
    font-size: 12px;
  }
  .main-screen__block-button{
    margin-top: -40px;
  }
  .footer__wrapper{
    flex-direction: column;
  }
  .footer__info{
    margin-right: 0;
    
  }
  .footer__info-ul{
    padding-left: 0;
  }
  .footer__links-ul{
    padding-left: 0;
  }
  .footer{
    padding-top: 25px;
  }
  .footer__logo{
    font-size: 20px;
  }
  .horizontal-row{
    left: 0;
  }
  .about-us__item-title{
    font-size: 24px;
    margin-bottom: 10px;
    margin-top: 16px;
  }
  .about-us__item-descr{
    font-size: 16px;
  }
  .about-us__item-button{
    margin-top: -40px;
    a{
      button{
        width: 161px;
        padding: 5px 0 5px 0;
        font-size: 13px;
        
      }
    }
  }
  .faq__title{
    font-size: 18px;
    margin-bottom: -45px;
  }
  .about-us{
    padding-bottom: 0;
  }
  
}
@media(max-width: 421px){
  .main-screen__block-title{
    font-size: 36px;
  }
  .main-screen__block-subtitle{
    font-size: 10px;
  }
  .main-screen__block-descr{
    font-size: 8px;
  }
  .btn{
    
    width: 236px;
    padding: 8px 0 8px 0;
    font-size: 14px;
    
   
  }
  
}
@media(max-width: 350px){
  .faq__item-title{
    font-size: 14px;
  }
  .faq__item-descr{
    font-size: 10px;
  }
  .main-screen__block-title{
    font-size: 31px;
  }
  .main-screen__block-descr{
    letter-spacing: 1px;
  }
  .main-sreen__block-button-descr{
    font-size: 8px;
  }
  .faq__links{
    font-size: 11px;
  }
}